  import test from '../../../static/js/apy.js'
	export default {
		data() {
			return {
			  api:this.GLOBAL.baseURL,
				country:"",
				countryArr:[],
				isAddcard:false,
					card:'',
					name:"",
					bank:'',
         bankList:[],
        saveList:[],
        deletBank:false
			};
		},
    watch:{
		    card:function(){
            var v = this.card;
            if(/\S{5}/.test(v)){
              this.card = v.replace(/\s/g, '').replace(/(.{4})/g, '$1 ');
            }
        }

    },
    methods:{
		  saveBank(){
		    this.$http.post(this.api + '/banks',{
		      token: localStorage.getItem('token'),
          bank_name: this.bank,
          card_number: this.card,
          user_name: this.name
        })
          .then(res=>{
            if(res.data.code == 1){
              this.getBank()
              this.isAddcard = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/Index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.saveBank()
                })
            }else{
              alert(res.data.msg)
            }
          })
      },
      getBank(){
		    this.$http.post(this.api + '/bankl',{
		      token: localStorage.getItem('token'),
        })
          .then(res=>{
            if(res.data.code == 1){
              this.saveList = res.data.data
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getBank()
                })
            }else{
              alert(res.data.msg)
            }
          })
      },
      delet(){
		    this.deletBank = !this.deletBank
      },
      deleteBnak(item,index){
        this.$http.post(this.api + '/bankd',{
          token: localStorage.getItem('token'),
          bank_id: item.bank_id
        })
          .then(res=>{
            if(res.data.code == 1){
              this.saveList.splice(index,1)
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.deleteBnak(item,index)
                })
            }else{
              alert(res.data.msg)
            }
          })
      }
    },
    mounted() {
		  this.bankList = test()
      this.getBank()
    }
  }



